import React, {useEffect, useState, useContext} from 'react';
import { Link, Redirect } from 'react-router-dom';
import useFetch from 'hooks/useFetch';
import useLocalStorage from 'hooks/useLocalStorage';
import { CurrentUserContext } from 'contexts/currentUser';
import BackendErrorMessages from 'components/backendErrorMessages';

const Authontication = (props) => {
    const isLogin = props.match.path === '/login';
    const pageTitle = isLogin ? 'Sign In' : 'Sign Up';
    const descriptionLink = isLogin ? '/register' : '/login';
    const descrText = isLogin ? 'Need an account' : 'Have an account?';
    const apiUrl = isLogin ? '/users/login' : '/users';
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [username, setUsername] = useState('');
    const [isSuccessfullSubmit, setIsSuccessfullSubmit] = useState(false);
    const [{response, error, isLoading}, doFetch] = useFetch(apiUrl);
    const [, setToken] = useLocalStorage('token');
    const [currentUser, setCurrentUserState] = useContext(CurrentUserContext);
    
    const handleSubmit = (e) => {
        e.preventDefault();
        let user = isLogin ? {email, password} : {email, password, username};

        doFetch({
            method: 'post',
            data: {
              user
            }
          })
        setPassword('');
        setEmail('');
        if(!isLogin) {
            setUsername('');
        }
    } 

    useEffect(() => {
        if(!response) {
            return;
        }
        setToken(response.user.token);
        setIsSuccessfullSubmit(true);
        setCurrentUserState(state => ({
            ...state,
            isLoggedIn: true,
            isLoaiding: false,
            currentUser: response.user
        }))
    }, [response, setToken, setCurrentUserState]);

    if(isSuccessfullSubmit) {
        return <Redirect to='/'/>
    }

    return (
        <div className="auth-page">
            <div className="container page">
                <div className="row">
                    <div className="col-md-6 offset-md-3 col-xs-12">
                        <h1 className='text-xs-center'>{pageTitle}</h1>
                        <p className="text-xs-center">
                            <Link to={descriptionLink}>{descrText}</Link>
                        </p>
                        <form onSubmit={handleSubmit}>
                            {error && <BackendErrorMessages backendErrors={error.errors}/>}
                            <fieldset>
                                {!isLogin && (
                                    <fieldset className='form-group'>
                                        <input 
                                        type="text" 
                                        className="form-control form-control-lg" 
                                        placeholder='Username'
                                        value={username}
                                        onChange={e => setUsername(e.target.value)} />
                                    </fieldset>
                                )}
                                <fieldset className='form-group'>
                                    <input 
                                    type="email" 
                                    className="form-control form-control-lg" 
                                    placeholder='Email'
                                    value={email}
                                    onChange={e => setEmail(e.target.value)} />
                                </fieldset>
                                <fieldset className='form-group'>
                                    <input 
                                    type="password" 
                                    className="form-control form-control-lg" 
                                    placeholder='Password' 
                                    value={password}
                                    onChange={e => setPassword(e.target.value)}
                                    />
                                </fieldset>
                                <button 
                                className='btn btn-lg btn-primary pull-xs-right' 
                                type='submit'
                                disabled={isLoading}>{pageTitle}</button>
                            </fieldset>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Authontication;